import React, { useRef, useEffect } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';

const ThreeScene = () => {
    const sceneRef = useRef(null);

    useEffect(() => {
        // Khi to scene, camera, renderer, v cc bin cn thit

        const camera = new THREE.PerspectiveCamera(40, window.innerWidth / window.innerHeight, 0.01, 10);
        camera.position.set(3, 3, 1);
        camera.lookAt(0, 0, 0);

        const scene = new THREE.Scene();
        scene.background = new THREE.Color(0xffffff);

        const light = new THREE.HemisphereLight(0xbbbbff, 0x444422);
        light.position.set(0, 1, 0);
        scene.add(light);

        const mesh = new THREE.Mesh(
            new THREE.PlaneGeometry(6, 6),
            new THREE.MeshPhongMaterial({ color: 0x999999, depthWrite: false })
        );

        mesh.rotation.x = -Math.PI / 2;
        mesh.receiveShadow = true;
        scene.add(mesh);

        const grid = new THREE.GridHelper(6, 20, 0x000000, 0x000000);
        grid.material.opacity = 0.1;
        grid.material.transparent = true;
        scene.add(grid);
        const maxContainerWidth = 1000;
        const maxContainerHeight = 1000;
        const scaleFactor = Math.min(maxContainerWidth / window.innerWidth, maxContainerHeight / window.innerHeight);
        // ... (thm code khi to)

        // Khi to GLTFLoader v ti model
        const loader = new GLTFLoader();
        loader.load('https://raw.githubusercontent.com/AnhKiet259/Test3D/master/mohinh.glb', (gltf) => {
            // X l m hnh 3D
            const model = gltf.scene;
            // ... (thm code x l m hnh)

            // Thm m hnh vo scene
            scene.add(model);

            // Cp nht scene khi model  c ti
            sceneRef.current.add(scene);

            // Khi to OrbitControls
            const controls = new OrbitControls(camera, sceneRef.current);
            controls.target.set(0, 1, 0);
            controls.update();
        });

        // Khi to renderer v thm vo DOM
        const renderer = new THREE.WebGLRenderer({ antialias: true });
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
        document.body.appendChild(renderer.domElement);

        // X l s kin thay i kch thc ca s
        window.addEventListener('resize', onWindowResize, false);

        // Hm x l thay i kch thc ca s
        function onWindowResize() {
            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();
            renderer.setSize(window.innerWidth, window.innerHeight);
        }

        // Hm animate  to vng lp render
        function animate() {
            requestAnimationFrame(animate);
            renderer.render(scene, camera);
        }

        // Gi hm animate  bt u vng lp render
        animate();

        // Cleanup
        return () => {
            window.removeEventListener('resize', onWindowResize);
            document.body.removeChild(renderer.domElement);
        };
    }, []); // Ch chy mt ln khi component c mount

    return <div ref={sceneRef} />;
};

export default ThreeScene;
